<template>
    <v-container>
        <v-card>
            <v-container grid-list-md mb-0>
                <h2 class="text-md-center">Data Batasan Bet CPKB</h2>
                <v-layout row wrap style="margin:10px">
                    <v-flex xs6>
                        <v-btn depressed dark rounded style="text-transform: none !important;" color="green accent-3"
                            @click="dialog = true">
                            <v-icon size="18" class="mr-2">mdi-pencil-plus</v-icon>
                            Tambah Batasan
                        </v-btn>
                    </v-flex>
                    <v-flex xs6 class="text-right">
                        <v-text-field v-model="keyword" append-icon="mdi-search" label="Search" hide-details>
                        </v-text-field>
                    </v-flex>
                </v-layout>
                <v-data-table :headers="headers" :items="params" :search="keyword" :loading="load">
                    <template v-slot:body="{ items }">
                        <tbody>
                            <tr v-for="(item,index) in items" :key='item["Nama"]'>
                                <td class="title">{{ index + 1 }}</td>
                                <td class="title">{{ item["Nama"] }}</td>
                                <td class="title">{{ item["Isi"] }}</td>
                                <td>
                                    <v-btn icon color="indigo" light @click="editHandler(item)" title="Edit">
                                        <v-icon>mdi-pencil</v-icon>
                                    </v-btn>
                                    <v-btn icon color="error" light @click='deleteData(item["Nama"])' title="Delete">
                                        <v-icon>mdi-delete</v-icon>
                                    </v-btn>
                                </td>
                            </tr>
                        </tbody>
                    </template>
                    
                </v-data-table>
            </v-container>
        </v-card>
        <v-dialog v-model="dialog" persistent max-width="600px">
            <v-card>
                <v-card-title>
                    <span class="headline">Tambah Batasan Bet</span>
                </v-card-title>
                <v-card-text>
                    <v-container>
                        <v-row no-gutters>
                            <v-col cols="4" v-if="this.statusUpdate === true">
                                <h2 class="mt-2">Nama</h2>
                            </v-col>
                            <v-col class="ml-4" cols="7" v-if="this.statusUpdate === true">
                                <v-text-field dense single-line outlined label="Nama" class="title mr-4" v-model="form.nama" required>
                                </v-text-field>
                            </v-col>
                            <v-col cols="4" v-if="this.statusUpdate === false">
                                <h2 class="mt-2">Nama</h2>
                            </v-col>
                            <v-col class="ml-4" cols="7" v-if="this.statusUpdate === false">
                                <v-text-field dense single-line outlined label="Nama" class="title mr-4" v-model="form.nama" readonly>
                                </v-text-field>
                            </v-col>
                            <v-col cols="4">
                                <h2 class="mt-2">Isi</h2>
                            </v-col>
                            <v-col class="ml-4" cols="7">
                                <v-text-field dense single-line outlined label="Isi" class="title mr-4" v-model="form.isi" required>
                                </v-text-field>
                            </v-col>
                        </v-row>
                    </v-container>
                    <small>*Isi Dimasukkan Sesuai Digit, Contoh : <br>  012 diisi 12 <br> 001 diisi 1 <br> 150 diisi 150</small>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="red white--text" dense rounded @click="close">Close</v-btn>
                    <v-btn class="ml-4 white--text" color="blue darken-1" rounded @click="setForm()">Save</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-snackbar v-model="snackbar" :color="color" :multi-line="true" :timeout="3000">
            {{ text }}
            <v-btn dark text @click="snackbar = false">
                Close
            </v-btn>
        </v-snackbar>
    </v-container>
</template>
<script>
    export default {
        data() {
            return {
                statusUpdate: true,
                dialog: false,
                keyword: '',
                headers: [{
                        text: 'No',
                        value: 'no',
                        class: 'title',
                    },
                    {
                        text: 'Nama',
                        value: 'Nama',
                        class: 'title',
                    },
                    {
                        text: 'Isi',
                        value: 'Isi',
                        class: 'title',
                    },
                    {
                        text: 'Aksi',
                        value: '',
                        class: 'title',
                    },
                ],
                params: [],
                snackbar: false,
                color: null,
                text: '',
                load: false,
                form: {
                    nama: '',
                    isi: '',
                },
                param: new FormData,
                typeInput: 'new',
                errors: '',
                updatedId: '',
            }
        },
        methods: {
            getData() {
                var uri = this.$apiUrl + 'batasanbetcpkbs'
                this.$http.get(uri).then(response => {
                    this.params = response.data
                })
            },
            Activity(key) {
                // untuk memposting aktivitas user yang login
                var activity = new FormData
                activity.append('activity', key)
                var uri = this.$apiUrl + 'activity'
                this.$http.post(uri, activity).then(response => {}).catch(error => {
                    this.errors = error
                })
            },
            sendData() {
                this.param.append('nama', this.form.nama);
                this.param.append('isi', this.form.isi);
                var uri = this.$apiUrl + 'batasanbetcpkbs'
                this.load = true
                this.$http.post(uri, this.param).then(response => {
                    this.snackbar = true; //mengaktifkan snackbar
                    this.color = 'green'; //memberi warna snackbar
                    this.text = response.data; //memasukkan pesan ke snackbar
                    this.load = false;
                    this.dialog = false
                    this.getData(); //mengambil data user
                    this.resetForm();
                    this.Activity('Melakukan Penambahan Data Batasan Bet CPKB');
                }).catch(error => {
                    this.errors = error
                    this.snackbar = true;
                    this.text = 'Try Again';
                    this.color = 'red';
                    this.load = false;
                })
            },
            updateData() {
                this.param.append('isi', this.form.isi);
                var uri = this.$apiUrl + 'batasanbetcpkbs/' + this.updatedId;
                this.load = true
                this.$http.post(uri, this.param).then(response => {
                    this.snackbar = true; //mengaktifkan snackbar
                    this.color = 'green'; //memberi warna snackbar
                    this.text = response.data; //memasukkan pesan ke snackba
                    this.load = false;
                    this.dialog = false
                    this.statusUpdate = true
                    this.getData(); //mengambil data user
                    this.resetForm();
                    this.Activity('Melakukan Perubahan Batasan Bet CPKB');
                    this.typeInput = 'new';
                }).catch(error => {
                    this.errors = error
                    this.snackbar = true;
                    this.text = 'Try Again';
                    this.color = 'red';
                    this.load = false;
                    this.typeInput = 'new';
                })
            },
            
            editHandler(item) {
                this.typeInput = 'edit';
                this.statusUpdate = false
                this.dialog = true;
                this.form.nama = item["Nama"];
                this.form.isi = item["Isi"];
                this.updatedId = item["Nama"];
            },
            deleteData(deleteId) { //mengahapus data
                var uri = this.$apiUrl + 'batasanbetcpkbs/' + deleteId; //data dihapus berdasarkan id
                this.$http.delete(uri).then(response => {
                    this.snackbar = true;
                    this.text = response.data;
                    this.color = 'green'
                    this.deleteDialog = false;
                    this.getData();
                    this.Activity('Melakukan Delete Batasan Bet CPKB');
                }).catch(error => {
                    this.errors = error
                    this.snackbar = true;
                    this.text = 'Try Again';
                    this.color = 'red';
                })
            },
            setForm() {
                if (this.typeInput === 'new') {
                    this.sendData()
                    
                } else {
                    this.updateData()
                }
            },
            close() {
                this.dialog = false
                this.resetForm()
            },
            resetForm() {
                this.form = {
                    nama: '',
                    isi: '',
                },
                this.statusUpdate = true
            }
        },
        mounted() {
            this.getData();
        },
    }
</script>